
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: { XHeader, GroupTitle, Flexbox, FlexboxItem, XButton },
data() {
return {
departmentid:this.$store.getters.user.departmentid,
partyBranch:'',
address:'',
honor:'',
people:''


}
},
methods: {
getDepartment(){
axios({
method: 'get',
url: '/dangjian/pdepartment/queryById',
params: {
departmentid:this.departmentid

}
}) .then((res)=> {
this.partyBranch=res.data.partyBranch;
this.address=res.data.address;
this.honor=res.data.honor;
this.people=res.data.people;


console.log(res.data)
}).catch(function (error) {
console.log(error);
})
}

},mounted() {
this.getDepartment()
}


};



